$(function() {
  //Swiper 
  const swiperArtist = new Swiper(".swiper-artist", {
    slidesPerView: 1,
    spaceBetween: 0,
    //
    breakpoints: {
      1200: {
        centeredSlides:true,
        slidesPerView: 3,
        spaceBetween: 24,
      }
    },
    loop: true,
    pagination: {
      el: ".swiper-pagination"
    },
    speed: 1000,
    autoplay: {
      disableOnInteraction: false,
      delay: 2500
    },
  });

  // imagesLoaded :
  // 
  $('.artwork').imagesLoaded().progress( function() {
    $('.artwork').masonry(); // 
  });

  // 
  $('.explore').imagesLoaded().progress( function() {
    $('.explore').masonry(); // 
  });
});